import React, { useState } from "react";
import { Map, Marker, Popup, TileLayer } from "react-leaflet";
import Routing from "../config/RoutingMachine";
const Maps = () => {
  const [position, setPosition] = useState([-6.1928813, 106.8542165]);
  const [isMapInit, setIsMapInit] = useState(false);
  const [map, setMap] = useState();

  const saveMap = (data) => {
    setMap(data);
    setIsMapInit(true);
  };

  return (
    <Map
      style={{ height: 605, width: "82%", marginLeft: "18%" }}
      center={position}
      zoom={20}
      scrollWheelZoom={true}
      ref={saveMap}
    >
      <TileLayer
        attribution='&copy; <a href="http://osm.org/copyright"></a> contributors'
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />
      {/* <Marker position={position}>
          <Popup>Posisi Kantor Polisi</Popup>
        </Marker> */}
      {isMapInit && <Routing map={map} />}
    </Map>
  );
};
export default Maps;
